.chapter {
	display: table;
	background-color: white;
	padding: 16px;
	width: 100%;
	border-top: 1px solid #ddd;
}

.chapter .name {
	display: table-cell;
    vertical-align: middle;
	float: left;
	min-height: 22px;
	width: calc(100% - 22px);
}

.chapter .arrow {
	display: table-cell;
    vertical-align: middle;
	width: 22px;
	min-height: 22px;
	float: right;
}

.chapter .arrow:before,
.chapter .arrow:after  {
	display: block;
	content: "";
	width: 15px;
	height: 3px;
	background-color: #ddd;
	border-radius: 3px;
}

.chapter .arrow:before {
	position: absolute;
	-webkit-transform:rotate(45deg);
	        transform:rotate(45deg);
	top: 5px;
}

.chapter .arrow:after {
	position: absolute;
	-webkit-transform:rotate(-45deg);
	        transform:rotate(-45deg);
	bottom: 5px;
}

