.section {
	margin: 32px 0px;
}

.section a{
	color: #404040;
	letter-spacing: 3px;
}

.section:nth-child(1) {
	margin-top: 0px;
}

.section:nth-last-child(1) {
	margin-bottom: 0px;
}

.section .top {
	height: 200px;
	background-color:#ddd;
	position: relative;
	color: #fff;
	background-size: cover;
	text-shadow: 1px 1px #404040;
	padding: 16px;
}

.section > .top div {
	position: absolute;
	bottom: 32px;
}

.section > .top h1{
	font-size: 2.8rem;
	font-weight: bold;
	letter-spacing: 3px;
}

.section > .top p{
	font-size: 1.6rem;
	font-weight: bold;
	letter-spacing: 3px;
	font-style: italic;
}

.chapter {
	display: table;
	background-color: white;
	padding: 16px;
	width: 100%;
	border-top: 1px solid #ddd;
}

.chapter .name {
	display: table-cell;
    vertical-align: middle;
	float: left;
	min-height: 22px;
	width: calc(100% - 22px);
}

.chapter .arrow {
	display: table-cell;
    vertical-align: middle;
	width: 22px;
	min-height: 22px;
	float: right;
}

.chapter .arrow:before,
.chapter .arrow:after  {
	display: block;
	content: "";
	width: 15px;
	height: 3px;
	background-color: #ddd;
	border-radius: 3px;
}

.chapter .arrow:before {
	position: absolute;
	transform:rotate(45deg);
	top: 5px;
}

.chapter .arrow:after {
	position: absolute;
	transform:rotate(-45deg);
	bottom: 5px;
}